import React, { useEffect, useRef, useState } from 'react';
import { useListenToEmotions } from "@/lib/emotionContext";
import { getSpriteSheetPath, getAnimationFrames, spriteDimensions } from './SpriteManager';
import { getColorSpecificAnimation, getHealthState, getStatusMessage } from './SpriteAdapter';
import { getSpriteDataUrl, shouldUseSpriteData } from './SpriteData';

// Define all possible evolution stages and animation states
export type LilGuyStage = 'egg' | 'hatchling' | 'normal' | 'angel' | 'devil';
export type LilGuyColor = 'green' | 'blue' | 'black';
export type LilGuyAnimation = 'idle' | 'walk' | 'happy' | 'angry' | 'sad' | 'shocked' | 'shake' | 'hatch';

interface PixelLilGuyProps {
  showControls?: boolean;
  size?: 'normal' | 'widget';
  className?: string;
  initialAnimation?: LilGuyAnimation;
  initialStage?: LilGuyStage;
  initialColor?: LilGuyColor;
  health?: number;
}

// Helper function to safely access localStorage
const getLocalStorageItem = (key: string, defaultValue: any) => {
  if (typeof window !== 'undefined') {
    const item = localStorage.getItem(key);
    return item ? (typeof defaultValue === 'object' ? JSON.parse(item) : item) : defaultValue;
  }
  return defaultValue;
};

// Helper function to safely set localStorage item
const setLocalStorageItem = (key: string, value: any) => {
  if (typeof window !== 'undefined') {
    localStorage.setItem(key, typeof value === 'object' ? JSON.stringify(value) : value);
  }
};

const PixelLilGuy: React.FC<PixelLilGuyProps> = ({
  showControls = false,
  size = 'normal',
  className = '',
  initialAnimation = 'idle',
  initialStage = 'normal',
  initialColor = 'green', // Set green as the default color
  health = 50,
}) => {
  // State for character
  const [animation, setAnimation] = useState<LilGuyAnimation>(initialAnimation);
  const [stage, setStage] = useState<LilGuyStage>(initialStage);
  const [color, setColor] = useState<LilGuyColor>(initialColor);
  const [name, setName] = useState<string>('LilGuy');
  const [message, setMessage] = useState<string>('');
  const [characterHealth, setCharacterHealth] = useState<number>(health);
  const [healthState, setHealthState] = useState<'low' | 'medium' | 'high'>('medium');
  const [useFallback, setUseFallback] = useState<boolean>(false);

  // Animation frame tracking
  const animationFrameRef = useRef<number | null>(null);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const frameCountRef = useRef<number>(0);
  const [clickCount, setClickCount] = useState(0);
  const [isEditing, setIsEditing] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);
  
  // Add a ref to track loaded sprites
  const loadedSpritesRef = useRef<Record<string, HTMLImageElement>>({});

  // Update state from localStorage on client-side only
  useEffect(() => {
    setStage(getLocalStorageItem('lilGuyStage', initialStage) as LilGuyStage);
    setColor(getLocalStorageItem('lilGuyColor', initialColor) as LilGuyColor);
    setName(getLocalStorageItem('lilGuyName', 'LilGuy'));
    
    // Get health from localStorage
    const storedModifiedHealth = getLocalStorageItem('modifiedHealth', null);
    const storedWeeklyAverage = getLocalStorageItem('weeklyAverage', null);
    
    if (storedModifiedHealth) {
      setCharacterHealth(parseFloat(storedModifiedHealth));
    } else if (storedWeeklyAverage) {
      setCharacterHealth(parseFloat(storedWeeklyAverage));
    }
    
    // Set health state
    setHealthState(getHealthState(characterHealth));
    setIsInitialized(true);
  }, [initialStage, initialColor, characterHealth]);

  // Track health changes and update local storage
  useEffect(() => {
    const recalculateHealth = () => {
      setLocalStorageItem("modifiedHealth", characterHealth);
      setHealthState(getHealthState(characterHealth));
    };

    if (isInitialized) {
      recalculateHealth();
    }

    const handleStorageChange = (e: StorageEvent) => {
      if (e.key === "weeklyAverage") {
        const newAverage = getLocalStorageItem('weeklyAverage', 50);
        setCharacterHealth(parseFloat(newAverage));
      }
    };
    
    if (typeof window !== 'undefined') {
      window.addEventListener("storage", handleStorageChange);
      return () => {
        window.removeEventListener("storage", handleStorageChange);
      };
    }
  }, [characterHealth, isInitialized]);

  // Set initial status message
  useEffect(() => {
    if (isInitialized && message === '') {
      setMessage(getStatusMessage(characterHealth, stage));
    }
  }, [isInitialized, characterHealth, stage, message]);

  // Determine evolution based on goals and health
  useEffect(() => {
    const checkForEvolution = () => {
      const goals = JSON.parse(getLocalStorageItem('goals', '[]'));
      
      // Check if there are any goals
      if (goals.length === 0 && stage === 'egg') {
        // Keep as egg until first goal is created
        return;
      } else if (stage === 'egg' && goals.length > 0) {
        // If we have goals but still in egg stage, start hatching
        setAnimation('shake');
        setTimeout(() => {
          setAnimation('hatch');
          setTimeout(() => {
            setStage('hatchling');
            setLocalStorageItem('lilGuyStage', 'hatchling');
            
            // After hatching, wait a bit then evolve to normal
            setTimeout(() => {
              setStage('normal');
              setLocalStorageItem('lilGuyStage', 'normal');
              setAnimation('happy');
              setTimeout(() => setAnimation('idle'), 1000);
            }, 5000);
          }, 2000);
        }, 2000);
      }
      
      // Check for final evolution based on productivity score
      if (stage === 'normal') {
        if (characterHealth > 80) {
          setStage('angel');
          setLocalStorageItem('lilGuyStage', 'angel');
          setMessage('Your productivity is amazing! LilGuy evolved to angel form! ✨');
        } else if (characterHealth < 30) {
          setStage('devil');
          setLocalStorageItem('lilGuyStage', 'devil');
          setMessage('Your productivity is slipping. LilGuy evolved to devil form! 😈');
        }
      }
    };
    
    if (isInitialized) {
      checkForEvolution();
    
      // Set up interval to check periodically
      const evolutionInterval = setInterval(checkForEvolution, 10000);
      return () => clearInterval(evolutionInterval);
    }
  }, [stage, characterHealth, isInitialized]);

  // Listen to emotions from the app
  useListenToEmotions((emotion) => {
    switch (emotion.type) {
      case 'happy':
        setAnimation('happy');
        setTimeout(() => setAnimation('idle'), emotion.intensity === 100 ? 2000 : 600);
        
        // Generate encouraging messages
        if (emotion.intensity === 100) {
          const messages = [
            'Great job! Keep it up!',
            'You\'re on a roll!',
            'Awesome progress!',
            'That\'s the spirit!',
            'LilGuy is cheering for you!'
          ];
          setMessage(messages[Math.floor(Math.random() * messages.length)]);
        }
        break;
      case 'sad':
        setAnimation('sad');
        setTimeout(() => setAnimation('idle'), 600);
        
        // Generate supportive messages instead of negative ones
        const supportMessages = [
          'Take a deep breath. You can do this!',
          'It\'s okay to take a break!',
          'LilGuy believes in you!',
          'Remember your goals!',
          'Small steps still move forward!'
        ];
        setMessage(supportMessages[Math.floor(Math.random() * supportMessages.length)]);
        break;
      case 'angry':
        setAnimation('angry');
        setTimeout(() => setAnimation('idle'), 1000);
        break;
      case 'shocked':
        setAnimation('shocked');
        setTimeout(() => setAnimation('idle'), 800);
        break;
      case 'walk':
        setAnimation('walk');
        setTimeout(() => setAnimation('idle'), 2000);
        break;
      case 'idle':
        setAnimation('idle');
        break;
      default:
        setAnimation('idle');
        break;
    }
  });

  // Handle interaction with LilGuy
  const handleClick = () => {
    const newCount = clickCount + 1;
    setClickCount(newCount);
    
    if (newCount === 1) {
      setAnimation('happy');
      const greetings = [
        'Hi there!',
        'How\'s it going?',
        'Need help with something?',
        'Let\'s be productive today!',
        'Ready to achieve your goals?'
      ];
      setMessage(greetings[Math.floor(Math.random() * greetings.length)]);
      setTimeout(() => setAnimation('idle'), 1000);
    } else if (newCount >= 3) {
      setAnimation('walk');
      setMessage('Okay, I\'ll give you some space!');
      setTimeout(() => {
        setAnimation('idle');
        setClickCount(0);
        setMessage('');
      }, 2000);
    }
  };

  // Handle name editing
  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value);
  };
  
  const handleNameBlur = () => {
    setIsEditing(false);
    setLocalStorageItem('lilGuyName', name);
  };
  
  const handleNameKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleNameBlur();
    }
  };

  // Decide what emotion to show based on health
  useEffect(() => {
    if (isInitialized && animation === 'idle') {
      if (characterHealth < 20) {
        setAnimation('sad');
        setTimeout(() => setAnimation('idle'), 1000);
      } else if (characterHealth > 80) {
        setAnimation('happy');
        setTimeout(() => setAnimation('idle'), 1000);
      }
    }
  }, [characterHealth, animation, isInitialized]);

  // Preload sprite based on the current state
  const preloadSprite = (stage: LilGuyStage, color: LilGuyColor, animation: LilGuyAnimation): HTMLImageElement => {
    const spritePath = getSpriteSheetPath(stage, color, animation);
    const cacheKey = `${stage}_${color}_${animation}`;
    
    // Return cached sprite if it exists
    if (loadedSpritesRef.current[cacheKey]) {
      return loadedSpritesRef.current[cacheKey];
    }
    
    // Otherwise create and cache a new sprite
    const img = new Image();
    
    // Set up error handler before setting src
    img.onerror = () => {
      console.error(`Failed to load sprite: ${spritePath}. Using fallback.`);
      setUseFallback(true);
    };
    
    img.src = spritePath;
    loadedSpritesRef.current[cacheKey] = img;
    return img;
  };

  // Draw the pixel character on canvas
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Set canvas dimensions based on size prop
    let CANVAS_WIDTH: number, CANVAS_HEIGHT: number;
    if (size === 'widget') {
      CANVAS_WIDTH = canvas.width = 64;
      CANVAS_HEIGHT = canvas.height = 64;
    } else {
      CANVAS_WIDTH = canvas.width = 150;
      CANVAS_HEIGHT = canvas.height = 150;
    }

    // Draw placeholder character if sprites aren't loaded
    const drawPlaceholder = () => {
      if (!ctx) return;
      
      ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
      
      // Determine color based on character color
      const fillColor = color === 'green' ? '#4CAF50' : 
                       color === 'blue' ? '#2196F3' : 
                       '#333333'; // Black default
      
      // Drawing a simple placeholder character
      ctx.fillStyle = fillColor;
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 2;
      
      // Body
      const centerX = CANVAS_WIDTH / 2;
      const centerY = CANVAS_HEIGHT / 2;
      const size = Math.min(CANVAS_WIDTH, CANVAS_HEIGHT) * 0.6;
      
      // Draw rounded square body
      ctx.beginPath();
      ctx.roundRect(centerX - size/2, centerY - size/2, size, size, 10);
      ctx.fill();
      ctx.stroke();
      
      // Draw eyes
      const eyeSize = size * 0.2;
      const eyeY = centerY - size * 0.1;
      const eyeSpacing = size * 0.25;
      
      ctx.fillStyle = 'white';
      ctx.beginPath();
      ctx.ellipse(centerX - eyeSpacing, eyeY, eyeSize, eyeSize, 0, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
      
      ctx.beginPath();
      ctx.ellipse(centerX + eyeSpacing, eyeY, eyeSize, eyeSize, 0, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
      
      // Draw mouth based on animation
      ctx.beginPath();
      const mouthY = centerY + size * 0.2;
      if (animation === 'happy') {
        // Happy smile
        ctx.arc(centerX, mouthY, size * 0.3, 0, Math.PI, false);
        ctx.stroke();
      } else if (animation === 'sad') {
        // Sad frown
        ctx.arc(centerX, mouthY + size * 0.3, size * 0.3, Math.PI, 2 * Math.PI, false);
        ctx.stroke();
      } else {
        // Neutral line
        ctx.moveTo(centerX - size * 0.3, mouthY);
        ctx.lineTo(centerX + size * 0.3, mouthY);
        ctx.stroke();
      }
    };

    // In case we need to use generated sprites
    const useSpriteData = shouldUseSpriteData() && useFallback;

    // Apply color-specific animation overrides if needed  
    const currentAnimation = getColorSpecificAnimation(animation, color);
    
    // Try to load the sprite
    const playerImage = useSpriteData ? 
      // Create a new image with our generated data if needed
      (() => {
        const img = new Image();
        img.src = getSpriteDataUrl(color);
        return img;
      })() : 
      // Otherwise preload and use the file-based sprite
      preloadSprite(stage, color, currentAnimation);
    
    // Get animation definition for current state
    const animationDef = getAnimationFrames(stage, currentAnimation);
    
    // Scale based on container size
    const scale = size === 'widget' ? 0.12 : 0.3; // Adjusted for 500x500 sprites
    const displayWidth = spriteDimensions.width * scale;
    const displayHeight = spriteDimensions.height * scale;
    
    // Center sprite in canvas
    const centerX = (CANVAS_WIDTH - displayWidth) / 2;
    const centerY = (CANVAS_HEIGHT - displayHeight) / 2;

    // Animation loop function for sprite sheet
    function animate() {
      if (!ctx) return;
      
      ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
      
      // Since we're now using individual sprites for each animation,
      // we don't need to calculate frame positions within a sprite sheet
      
      // Draw the sprite
      try {
        ctx.drawImage(
          playerImage,
          0, 0, // Always start at top-left of image
          spriteDimensions.width, spriteDimensions.height,
          centerX, centerY,
          displayWidth, displayHeight
        );
      } catch (err) {
        // If drawing fails, fall back to placeholder
        console.error('Error drawing sprite:', err);
        drawPlaceholder();
        return;
      }

      // We still use frameCount to animate between different sprites later if needed
      frameCountRef.current++;
      animationFrameRef.current = requestAnimationFrame(animate);
    }

    // Start animation when image is loaded
    if (playerImage.complete) {
      // Image already loaded (or is a data URL)
      frameCountRef.current = 0;
      animate();
    } else {
      playerImage.onload = () => {
        frameCountRef.current = 0;
        animate();
      };
      
      // If the image fails to load, use placeholder
      playerImage.onerror = () => {
        console.error(`Failed to display sprite. Using placeholder.`);
        drawPlaceholder();
      };
    }

    // Cleanup on unmount or when animation changes
    return () => {
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };
  }, [animation, stage, color, size, useFallback]);

  // Create the character window with pixel art style
  return (
    <div className="pixel-window w-full max-w-xs mx-auto">
      <div 
        className="pixel-window-header"
        style={{ backgroundColor: color === 'green' ? 'var(--pixel-green)' : color === 'blue' ? 'var(--pixel-blue)' : 'var(--pixel-teal)' }}
      >
        {isEditing ? (
          <input
            type="text"
            value={name}
            onChange={handleNameChange}
            onBlur={handleNameBlur}
            onKeyDown={handleNameKeyDown}
            className="pixel-input bg-white text-black px-2 py-1 w-full"
            autoFocus
          />
        ) : (
          <div 
            className="cursor-pointer text-pixel-sm" 
            onClick={() => setIsEditing(true)}
          >
            {name}
          </div>
        )}
        <div className="pixel-window-controls">
          <div className="pixel-window-button"></div>
          <div className="pixel-window-button"></div>
          <div className="pixel-window-button"></div>
        </div>
      </div>
      
      <div className="pixel-window-content bg-gradient-to-b from-[#d4f4dc] to-[#b5e8e0] p-4 flex flex-col items-center">
        {/* Health meter */}
        <div className="w-full mb-2 px-1">
          <div className="text-pixel-sm mb-1 text-center">Health: {Math.round(characterHealth)}%</div>
          <div className="w-full bg-gray-200 h-4 pixel-border overflow-hidden">
            <div 
              className={`h-full ${characterHealth > 80 ? 'bg-green-500' : characterHealth < 30 ? 'bg-red-500' : 'bg-yellow-400'}`}
              style={{ width: `${characterHealth}%` }}
            ></div>
          </div>
        </div>
        
        {/* Canvas for the character sprite */}
        <div 
          className="relative cursor-pointer"
          onClick={handleClick}
        >
          <canvas 
            ref={canvasRef} 
            className={`pixelated ${className}`}
            style={{ imageRendering: 'pixelated' }}
          />
        </div>
        
        {/* Message bubble */}
        {message && (
          <div className="bg-white border-2 border-black rounded-lg p-3 mt-3 max-w-[90%] relative text-pixel-sm">
            <div className="absolute w-4 h-4 bg-white border-l-2 border-t-2 border-black transform rotate-45 -top-2 left-1/2 -translate-x-1/2"></div>
            {message}
          </div>
        )}
        
        {/* Control buttons for interactions */}
        {showControls && (
          <div className="flex justify-center gap-2 mt-4">
            <button 
              className="pixel-button pixel-button-secondary text-pixel-sm"
              onClick={() => {
                setAnimation('happy');
                setTimeout(() => setAnimation('idle'), 1000);
              }}
            >
              WATER
            </button>
            <button 
              className="pixel-button text-pixel-sm"
              onClick={() => {
                setAnimation('walk');
                setTimeout(() => setAnimation('idle'), 2000);
              }}
            >
              SUN
            </button>
            <button 
              className="pixel-button pixel-button-success text-pixel-sm"
              onClick={() => {
                setAnimation('angry');
                setTimeout(() => setAnimation('idle'), 1000);
              }}
            >
              GROW!!
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default PixelLilGuy;
